<template>
  <div>
    <div class="toolbar mb-2">
      <button @click.prevent="toggleBold" class="icon-btn" :class="{ 'active': isBoldActive }">
        <font-awesome-icon :icon="['fas', 'bold']" />
      </button>
      <button @click.prevent="toggleItalic" class="icon-btn" :class="{ 'active': isItalicActive }">
        <font-awesome-icon :icon="['fas', 'italic']" />
      </button>
      <button @click.prevent="toggleUnderline" class="icon-btn" :class="{ 'active': isUnderlineActive }">
        <font-awesome-icon :icon="['fas', 'underline']" />
      </button>
      <button @click.prevent="toggleSuperscript" class="icon-btn" :class="{ 'active': isSuperscriptActive }">
        <font-awesome-icon :icon="['fas', 'superscript']" />
      </button>
      <button @click.prevent="toggleSubscript" class="icon-btn" :class="{ 'active': isSubscriptActive }">
        <font-awesome-icon :icon="['fas', 'subscript']" />
      </button>
      <button @click.prevent="toggleBulletList" class="icon-btn" :class="{ 'active': isBulletListActive }">
        <font-awesome-icon :icon="['fas', 'list-ul']" />
      </button>
      <button @click.prevent="toggleOrderedList" class="icon-btn" :class="{ 'active': isOrderedListActive }">
        <font-awesome-icon :icon="['fas', 'list-ol']" />
      </button>
      <button @click.prevent="setLink" class="icon-btn" :class="{ 'active': isLinkActive }">
        <font-awesome-icon :icon="['fas', 'link']" />
      </button>
      <button @click.prevent="unsetLink" class="icon-btn" :class="{ 'disabled': !isLinkActive }" :disabled="!isLinkActive">
        <font-awesome-icon :icon="['fas', 'unlink']" />
      </button>

    </div>
    <div
        class="editor-textarea editor-container bg-gray-50 border border-gray-400 text-gray-900 text-sm w-full rounded-lg focus-within:ring-blue-500 focus-within:border-blue-500"
        @click="focusEditor">
      <EditorContent :editor="editor"/>
    </div>
  </div>
</template>

<script setup>
import { useEditor, EditorContent } from '@tiptap/vue-3'
import StarterKit from '@tiptap/starter-kit'
import Document from '@tiptap/extension-document'
import Text from '@tiptap/extension-text'
import TextStyle from '@tiptap/extension-text-style'
import FontFamily from '@tiptap/extension-font-family'
import Color from '@tiptap/extension-color'
import Typography from '@tiptap/extension-typography'
import Underline from '@tiptap/extension-underline'
import Subscript from '@tiptap/extension-subscript'
import Superscript from '@tiptap/extension-superscript'
import BulletList from '@tiptap/extension-bullet-list'
import OrderedList from '@tiptap/extension-ordered-list'
import Link from '@tiptap/extension-link'
import { computed, onBeforeUnmount, onMounted, onUnmounted, ref } from 'vue'

const emits = defineEmits(['updateContent'])

const props = defineProps({
  description: String,
  placeholder: String,
  isFocused: {
    type: Boolean,
    default: true
  },
})

const initialContent = props.description
    ? props.description
    : (props.placeholder ? props.placeholder : '<p>Start typing the description...</p>')

const editor = useEditor({
  extensions: [
    StarterKit,
    TextStyle,
    FontFamily,
    Color,
    Typography,
    Underline,
    Subscript,
    Superscript,
    Link.configure({openOnClick: false}),
  ],
  content: initialContent,
  onUpdate: ({editor}) => {
    const htmlOutput = editor.getHTML()
    emits('updateContent', htmlOutput)
  },
})

const hasFocused = ref(false) // State to track if the editor has been focused

const focusEditor = (event) => {
  if (!hasFocused.value) {
    if (!props.description) {
      // Clear the editor the first time it's focused
      editor.value.commands.clearContent()
    }
    hasFocused.value = true
  }
  editor.value.commands.focus()
}
const toggleBold = () => {
  editor.value.chain().focus().toggleBold().run()
}

const toggleItalic = () => {
  editor.value.chain().focus().toggleItalic().run()
}

const toggleUnderline = () => {
  editor.value.chain().focus().toggleUnderline().run()
}

const toggleSuperscript = () => {
  editor.value.chain().focus().toggleSuperscript().run()
}

const toggleSubscript = () => {
  editor.value.chain().focus().toggleSubscript().run()
}

const toggleBulletList = () => {
  editor.value.chain().focus().toggleBulletList().run()
}

const toggleOrderedList = () => {
  editor.value.chain().focus().toggleOrderedList().run()
}


const setLink = () => {
  const previousUrl = editor.value.getAttributes('link').href
  const url = window.prompt('URL', previousUrl)
  if (url === null) return

  if (url === '') {
    editor.value.chain().focus().extendMarkRange('link').unsetLink().run()
  } else {
    editor.value.chain().focus().extendMarkRange('link').setLink({ href: url }).run()
  }
}

const unsetLink = () => {
  if (editor.value) {
    editor.value.chain().focus().extendMarkRange('link').unsetLink().run()
  }
}

const isBoldActive = computed(() => editor.value && editor.value.isActive('bold'))
const isItalicActive = computed(() => editor.value && editor.value.isActive('italic'))
const isUnderlineActive = computed(() => editor.value && editor.value.isActive('underline'))
const isSuperscriptActive = computed(() => editor.value && editor.value.isActive('superscript'))
const isSubscriptActive = computed(() => editor.value && editor.value.isActive('subscript'))
const isBulletListActive = computed(() => editor.value && editor.value.isActive('bulletList'))
const isOrderedListActive = computed(() => editor.value && editor.value.isActive('orderedList'))
const isLinkActive = computed(() => editor.value && editor.value.isActive('link'))


// const submitDescription = () => {
//   if (editor) {
//     const htmlOutput = editor.getHTML();
//     // Emit or use an API call to send `htmlOutput` to your backend
//     console.log(htmlOutput);
//     // Example: emit to parent component or handle form submission
//     emit('update:description', htmlOutput);
//   }
// }

onMounted(() => {
  // Ensure the editor is focused when needed and adjusts height accordingly
  editor.value?.commands.focus()
  adjustHeight()
  const editorElement = document.querySelector('.ProseMirror')  // Adjust the selector as needed

})

const adjustHeight = () => {
  const content = editor.value.view.dom // Access the editor's content DOM node
  if (content) {
    content.style.minHeight = 'auto' // Reset minHeight to calculate potentially smaller size
    content.style.minHeight = `${Math.max(content.scrollHeight, 150)}px` // Set minHeight based on content or minimum
  }
}

// Add this as a handler for content updates
editor.value?.on('update', adjustHeight)

// Ensure editor is destroyed properly on component unmount
onBeforeUnmount(() => {
  if (editor.value) {
    editor.value.destroy()
  }
})

</script>
<style scoped>
.toolbar {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.icon-btn {
  background-color: #4a5568;
  color: white;
  padding: 8px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  transition: background-color 0.3s, transform 0.3s;
}

.icon-btn.active {
  background-color: #2d3748;
}

.icon-btn:hover {
  background-color: #2d3748;
  transform: scale(1.1);
}

.icon-btn.disabled {
  background-color: #a0aec0;
  cursor: not-allowed;
}

.icon-btn i {
  font-size: 16px;
}
</style>
